.tabs {
    position: relative;
    margin-bottom: 24px;
}
.tabs:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0px;
    height: 1px;
    background-color: #d7d7d7;
}
.sessions {
    max-width: 1000px;
    margin: 0 auto;
    padding-top: 24px;
}
.row {
    display: flex;
    align-items: center;
    white-space: nowrap;
}
.nowrap {
    display: flex;
    white-space: nowrap;
}
.headerRow {
    padding-bottom: 4px;
    border-bottom: 1px solid #d7d7d7;
    height: 66px;
}
.rows {
    padding-left: 8px;
    min-height: 500px;
}
.textField {
    font-size: 11px;
}
.menuItem {
    height: 16px !important;
}
.select {
    margin-left: 16px;
}
.selectWrapper {
    margin-left: 32px;
}
.inputLabel {
    color: #777 !important;
    font-size: 0.9em !important;
}
.rank, .date {
    margin-left: 16px;
    color: #777;
    font-size: 0.8em;
}