.sessionDetails {
    padding: 0 16px;
}
.card {
    max-width: 800px;
    margin: 0 auto;
    box-sizing: border-box;
    text-align: left;
    padding: 0 8px;
}
.personDetails {
    margin-top: 24px;
    margin-bottom: 24px;
    display: flex;
    text-align: left;
}
.personDetails span {
    margin: 0 16px;
}
.name {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 4px;
}
.light {
    color: #777;
    font-size: 14px;
    line-height: 1.4;
}
.cardContent {
    position: relative;
    padding: 16px !important;
}
.highlights {
    margin: 16px auto 0 auto;
}
.title {
    margin-bottom: 16px !important;
    font-size: 12px !important;
    color: #999 !important;
}
.icon {
    vertical-align: middle;
}
.arrowUp {
    color: green;
}
.arrowDown {
    color: red;
}
.highlightsText {
    display: inline-block;
    width: 120px;
}
.highlightsContent {
    padding: 4px 0;
}
.loading {
    min-height: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.selectAlgorithm {
    position: absolute !important;
    top: 8px;
    right: 8px;
}
.menuItem {
    height: 12px !important;
    font-size: 14px !important;
}